body {
  font-family: "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  line-height: 1.5;
}

[tabindex='-1']:focus {
  outline: none !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

a {
  text-decoration: none;
  color: #2980b9;
}

a:focus, a:visited {
  color: #2980b9;
}

a:hover {
  text-decoration: underline;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
}

[role='button'] {
  cursor: pointer;
}

a,
area,
button,
[role='button'],
input,
label,
select,
summary,
textarea {
  touch-action: manipulation;
}

label {
  display: inline-block;
  margin-bottom: 0.5rem;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
textarea {
  line-height: inherit;
  margin: 0;
  border-radius: 0;
}

textarea {
  resize: vertical;
}

fieldset {
  min-width: 0;
  margin: 0;
  padding: 0;
  border: 0;
}

input[type='search'] {
  -webkit-appearance: none;
}

[hidden] {
  display: none !important;
}

body {
  color: #667273;
  background: linear-gradient(to right, #4e5758 0%, #363c3d 100%);
}

header {
  padding: 1.5rem 2.0rem;
}

header h1 {
  font-size: 2.4rem;
  font-weight: 300;
  line-height: 5rem;
  margin: 0;
  opacity: 0.80;
  color: #ffffff;
}

footer {
  position: absolute;
  right: 2.0rem;
  bottom: 2.0rem;
  opacity: 0.40;
  color: #ffffff;
}

footer a,
footer a:hover,
footer a:visited,
footer a:focus {
  color: #ffffff;
}

footer p {
  line-height: 1.0;
  margin-bottom: 0;
}

nav {
  position: absolute;
  top: 0;
  right: 0;
}

nav input.nav-menu-btn {
  display: none;
}

nav label.nav-menu-icon {
  position: absolute;
  top: 1.5rem;
  right: 2.0rem;
  display: inline-block;
  float: right;
  width: 5rem;
  height: 5rem;
  cursor: pointer;
  opacity: 0.8;
  border-radius: 50%;
  background: #ffffff;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="50" height="50"><line x1="15" x2="35" y1="19" y2="19" stroke-width="3" stroke="#363c3d" /><line x1="15" x2="35" y1="25" y2="25" stroke-width="3" stroke="#363c3d" /><line x1="15" x2="35" y1="31" y2="31" stroke-width="3" stroke="#363c3d" /></svg>');
}

nav label.nav-menu-icon:hover {
  opacity: 1.0;
}

nav ul {
  display: none;
}

p {
  margin-top: 0;
}

small {
  font-size: 1.4rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 300;
  margin-top: 0;
  margin-bottom: 2.0rem;
}

h1 {
  font-size: 3.6rem;
  line-height: 1.2;
}

h2 {
  font-size: 2.8rem;
  line-height: 1.25;
}

h3 {
  font-size: 2.4rem;
  line-height: 1.3;
}

h4 {
  font-size: 2.0rem;
  line-height: 1.35;
}

h5 {
  font-size: 1.8rem;
  line-height: 1.5;
}

h6 {
  font-size: 1.6rem;
  line-height: 1.4;
}
